:root {
  --color--black-10: #0000001a;
  --color--black-40: #767676;
  --color--white: white;
  --color--white-40: #777777;
  --color--black-3: #00000008;
  --color--white-10: #ffffff1a;
  --color--black: black;
  --color--gold: #ffd700;
  --fonts--font-family: Geist, sans-serif;
}

html {
  scroll-behavior: smooth;
}  

body {
  font-family: Geist, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  line-height: 20px;
  overscroll-behavior: none;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
}

h1 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 38px;
  line-height: 44px;
}

h2 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 32px;
  font-weight: bold;
  line-height: 36px;
}

.wrap {
  z-index: 100;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
}

.wrap.hero {
  flex-flow: column;
  align-items: stretch;
  /* min-height: 100svh; */
}

.wrap.contact {
  margin-top: 10rem;
  padding-bottom: 2.5rem;
}

.grid {
  grid-column-gap: 1.25rem;
  grid-row-gap: 1.25rem;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  height: 100%;
  display: grid;
}

.grid-3 {
  grid-column-gap: 1.25rem;
  grid-row-gap: 1.25rem;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.grid-2 {
  grid-column-gap: 1.25rem;
  grid-row-gap: 1.25rem;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-auto-columns: 1fr;

  display: grid;
}

.tst {
  grid-column: span 3;
}
