.links {
  flex-flow: column;
  margin-top: 7.5rem;
  font-size: .75rem;
  letter-spacing: 0.02em;
  display: flex;
}

.logo {
  font-size: clamp(1rem, .928rem + .36vw, 1.25rem);
  display: inline-block;
}

.nav-links {
  width: 100%;
  line-height: 2.3;
  text-decoration: none;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
}

.nav-links:hover {
  color: var(--color--black-40);
  padding-left: .25rem;
}

.nav-links.w--current {
  opacity: 1;
  color: var(--color--black-40);
}

.nav-links.nav-links-dark, .logo-dark {
  color: var(--color--white);
}

.nav-links.nav-links-dark:hover, .logo-dark:hover, .nav-links.nav-links-dark.w--current {
  color: var(--color--white-40);
}

.nav-content {
  position: fixed;
  top: 2.5rem;
}

.hero_wrap, .inner-store-hero {
  /* margin-top: 25svh; */
  margin-top: 11.25rem;
  margin-bottom: 10rem;
}

.work-wrap {
  margin-top: 10rem;
  background-color: var(--color--black-3);
  padding: 2.5rem;
}

.work-wrap.work-wrap-dark {
  background-color: var(--color--white-10);
}

.cv-item {
  margin-top: 10rem;
  margin-bottom: 3.75rem;
}

.txt-item {
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}

.paragraph, .paragraph1 {
  column-count: 2;
  column-gap: 1.25rem;
  font-size: clamp(1rem, .986rem + .07vw, 1.05rem);
  line-height: 1.6;
}
.paragraph1 {
  column-count: 1;
}

.feat-items {
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}

.name {
