  margin-top: 2.5rem;
  font-size: clamp(1rem, .986rem + .07vw, 1.05rem);
}

.subtxt.subtxt-dark {
  color: var(--color--white-40);
}

.img-works {
  aspect-ratio: 320 / 593;
  width: 100%;
}

.txt-xl {
  margin-top: 0;
  margin-bottom: 0;
  font-size: clamp(2.02rem, 1.598rem + 2.11vw, 3.5rem);
  line-height: 1;
  letter-spacing: -0.005em;
}

.txt-l {
