}

.txt-m {
  margin-bottom: 0;
  font-size: clamp(1.42rem, 1.326rem + .47vw, 1.8205rem);
  line-height: 1;
 letter-spacing: -0.003em;
}

.txt-s {
  margin-bottom: 0;
  font-size: clamp(1.125rem, 1.0893rem + 0.1786vw, 1.25rem);
  line-height: 1;
  letter-spacing: -0.003em;
