}

.buttons {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  grid-row-gap: 1.25rem;
}

.buttons > * {
  flex: 1;
}

.add-btn, .buy-btn {
  font-size: clamp(1rem, .986rem + .07vw, 1.05rem);
  font-weight: 500;
  letter-spacing: 0.02em;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  cursor: pointer;
  display: inline-block;
}

.add-btn {
  text-align: center;
  color: var(--color--black);
  text-decoration: none;
  padding: 1.25rem;
  border-radius: 999rem;
  border: 2px solid var(--color--black-40);
}
.add-btn:hover {
  border-color: var(--color--black);
}

.buy-btn {
  text-align: center;
  color: var(--color--white);
  background-color: var(--color--black);
  padding: 1.25rem;
  border-radius: 999rem;
}
.buy-btn:hover {
  background-color: var(--color--black-40);
}

.add-btn-dark {
  color: var(--color--white);
  border: 1px solid var(--color--white);

}

.buy-btn-dark {
  color: var(--color--black);
  background-color: var(--color--white);
}

.nav-links.nav-links-dark, .logo-dark {
  color: var(--color--white);
}
/* SWIPER */
.product-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 1.25rem;
}

/* Vertical thumbnails – let Swiper handle wrapper layout */
.product-thumbs {
  grid-column: span 1;
  max-width: 100%;
  height: 300px;
  overflow: hidden;
}
.product-thumbs img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  aspect-ratio: 1 / 1;
}
.product-thumbs .swiper-slide {
  height: auto;      /* Swiper will size them based on slidesPerView */
  opacity: 0.5;
  cursor: pointer;
}

.product-thumbs .swiper-slide-thumb-active {
  opacity: 1;
}

.product-main {
  grid-column: span 5;
  width: 100%;
  aspect-ratio: 6 / 7;
  padding: 2.5rem;;
}

.product-main img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.swiper-wrapper {
 background-color: var(--color--black-3);
}

.swiper-wrapper-dark {
background-color: var(--color--white-10);
}

/* Pagination (pick ONE style) */
.swiper-pagination-bullet {
  background: #000;
  opacity: 0.25;
}
.swiper-pagination-bullet-active {
  opacity: 1;
}

