}
.productdecription-grid {
  grid-area: 1 / 9 / 2 / 12;
}

.feat-grid {
  grid-area: 1 / 3 / 2 / 7;
}

.footercopy-grid {
  grid-area: 1 / 1 / 2 / 3;
