@media screen and (max-width: 767px) {
  .hero-grid {
    grid-column: 2 / 7;
  }

  .tt-link-grid {
    grid-column: 2 / 11;
    justify-self: start;
    grid-column-end: 7;
  }

  .cv-grid, .story-grid {
    grid-column: 2 / 7;
  }

  .feat-grid {
    grid-area: 1 / 2 / 2 / 5;
  }

  .footerimg-grid {
    grid-column: 2 / 5;
  }

  .footerlinks-grid {
    grid-column: 5 / 7;
    justify-self: start;
  }
   .products-grid {
    grid-column: 2 / 7;
  }
    .product-gallery {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .product-main {
    order: 1;
    width: 100%;
    aspect-ratio: 6 / 7; /* keep if you like this proportion on mobile */
  }

  .product-thumbs {
    order: 2;
