    height: auto;
    max-width: 100%;
    overflow: hidden;
  }

  .productdecription-grid {
    grid-area: 2/2/2/9;
  }

  /* Horizontal thumbs – Swiper handles direction, we control size */
  .product-thumbs .swiper-slide {
    flex: 0 0 80px;  /* ~80px wide thumbnails, tweak as needed */
